.page {
  background-color: beige;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

.container {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  gap: 48px 46px;
  padding-block: 50px;
  padding-inline: 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .container {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .container {
    --column-count: 4;
  }
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding-left: 50px;
  padding-right: 50px;
  display: flex;
}

.logo__img {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.nav__link.is-active {
  color: #00acdc;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 4px;
  height: 4px;
  display: block;
  position: relative;
  bottom: 4px;
}

.nav__link:hover {
  color: #00acdc;
}

.linkbtn {
  box-sizing: border-box;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 38px;
  text-decoration: none;
  display: block;
}

.linkbtn:hover {
  color: #00acdc;
  background-color: #fff;
  border-color: #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 12px;
  height: 12px;
  margin: 2px;
}

.stars__star:not(:last-child) {
  margin-right: 6px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 5px;
  width: 198px;
  height: 406px;
  padding: 16px;
  transition: all .3s;
}

.card__imageframe {
  width: 160px;
  height: 134px;
  margin: 16px auto 40px;
}

.card__image {
  width: 100%;
}

.card__title {
  color: #060b35;
  height: 36px;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  display: block;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card__code {
  color: #616070;
  text-align: left;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  height: 16px;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews {
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  height: 18px;
  margin-bottom: 16px;
  display: flex;
}

.card__price-name {
  text-align: left;
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-amount {
  text-align: right;
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}
